<template>
  <div class="c-buy-list c-in-cart" v-for="item in cartproduct" :key="item">
    <div class="c-buy-list-title"
      :class="{'is-pink' : item.type == 'room-trans', 'is-nile' : item.type == 'low-trans','is-green' : item.type == 'room-shop','is-purple' : item.type == 'low-shop','is-brown' : item.type == 'room-store','is-yellow' : item.type == 'low-store','is-gray' : item.type == 'cupon'}">
      <form action="" v-if="checkable">
        <input type="checkbox" @click="toggleModel(item.titletext)">
      </form>
      <div class="c-title-text">{{item.titletext}}</div>
      <div class="c-title-col c-title-number">數量</div>
      <div class="c-title-col c-title-gold">使用金幣</div>
      <div class="c-title-col c-title-price">價格</div>
    </div>
    <div class="c-buy-list-content" v-for="p in item.products" :key="p" :class="{'is-out-stock' : p.state == true}">
      <form action="" v-if="checkable">
        <input type="checkbox" :disabled="p.state == true">
      </form>
      <div class="c-buy-img">
        <img :src="p.img" alt="">
      </div>
      <div class="c-buy-name c-buy-pc">
        <div class="c-name-code">{{p.code}}</div>
        <div class="c-name-text">{{p.name}}</div>
        <div class="c-name-tip">{{p.tip}}</div>
      </div>
      <div class="c-buy-number c-buy-pc">
        <FormCounter v-if="p.state == false" />
      </div>
      <div class="c-buy-gold c-buy-pc">
        <template v-if="p.state == false">
          <input type="checkbox" :disabled="Number(userGold)<Number(item.gold)">
          <div class="c-buy-gold-flex">
            <span>{{item.gold}} /枚</span>
            <span v-if="Number(userGold)<Number(item.gold)"  class="c-buy-gold-shortage">金幣數量不足</span>
          </div>
        </template>
      </div>
      <div class="c-buy-price c-buy-pc">
        ${{p.price}}
      </div>
      <div class="c-buy-delet c-buy-pc"><span class="material-icons">delete</span></div>
      
      <div class="c-buy-info-mobile">
        <div class="c-buy-name">
          <div class="c-name-code">{{p.code}}</div>
          <div class="c-name-text">{{p.name}}</div>
          <div class="c-name-tip">{{p.tip}}</div>
        </div>
        <div class="g-d-flex" style="justify-content: space-between;">
          <div class="c-buy-gold">
            <template v-if="p.state == false">
              <input type="checkbox" :disabled="Number(userGold)<Number(item.gold)">
              <span>{{item.gold}} /枚 <span v-if="Number(userGold)<Number(item.gold)" class="c-buy-gold-shortage">金幣數量不足</span></span> 
            </template>
          </div>
          <div class="c-buy-price">
            ${{p.price}}
          </div>
        </div>
        <div class="g-d-flex" style="justify-content: end;">
          <div class="c-buy-number">
            <FormCounter v-if="p.state == false" />
          </div>

          <div class="c-buy-delet"><span class="material-icons">delete</span></div>
        </div>

      </div>

    </div>
    <ModalConfirm v-if="showModel" @close="showModel = false" :modaltype="'mini'"  :contentstyle="'top-line'">
        <template #header>
            <h5 class="g-modal-confirm">宅配小提醒</h5>
        </template>
        <template #content>
            <span class="">不同溫層商品不可同時結帳</span>
        </template>
        <template #footer>
            <div class="g-modal-cta g-d-wrap g-center">
                <div class="g-col-12">
                    <div class="g-btn g-btn-regular g-btn-main-line" @click="showModel = false">
                        確定
                    </div>
                </div>
            </div>
        </template>
    </ModalConfirm>
  </div>

</template>

<script>
  import FormCounter from '../../global/components/form/counter.vue'
  import ModalConfirm from '../../global/components/modal/ModalConfirm.vue'
  export default {
    name: 'CartProduct',
    props: {
      'cartproduct': {
        type: Object,
        require: false
      },
      'checkable': {
        type: Boolean,
        require: false
      },
      'userGold': {
        type: String,
        require: false
      }

    },
    components: {
      FormCounter,
      ModalConfirm
    },
    data() {
      return {
        showModel: false

      }
    },
    methods: {
      toggleModel(title) {
        if(title === '低溫宅配'){
          this.showModel = true
        }
      }
    }
  }
</script>

