<template>
  <swiper
    class="s-product-img-wrap"
    id="gallery"
    :slidesPerView="1"
    :centeredSlides="true"
    :simulateTouch="false"
    :thumbs="{ swiper: thumbsSwiper }"
    :modules="modules"
    @slideChange="currentSlide = $event.activeIndex"
  >
    <swiper-slide v-for="slide in carousel" :key="slide.id" class="s-product-img">
      <div
        class="carousel__item s-product-img"
        :class="{ 'g-product-video': slide.imgstyle == 'video' }"
        @click="slide.productvideoshow = true"
      >
        <img :src="slide.img" alt="" />
        <img v-if="alchol" src="@/global/assets/alchol.png" alt="">
      </div>
    </swiper-slide>
  </swiper>

  <swiper
    id="thumbnails"
    :slidesPerView="4"
    :rewind="true"
    :freeMode="true"
    :watchSlidesProgress="true"
    :modules="modules"
    class="s-product-list-img"
    @swiper="setThumbsSwiper"
  >
    <swiper-slide
      v-for="slide in carousel"
      :key="slide.id"
      :class="{
        'carousel__slide--active': currentSlide === slide.id - 1,
      }"
    >
      <div
        class="carousel__item g-carousel-img s-product-img"
        :class="{ 'g-product-video': slide.imgstyle == 'video' }"
        @click="slideTo(slide.id - 1)"
      >
        <img :src="slide.img" alt="" />
      </div>
    </swiper-slide>
  </swiper>

  <template v-for="s in carousel" :key="s">
    <ModalVideo
      :modalvideo="s.videolink"
      v-show="s.productvideoshow"
      @close="s.productvideoshow = false"
      v-if="s.imgstyle == 'video'"
    />
    <template v-else></template>
  </template>
</template>

<script>
import ModalVideo from '../modal/ModalVideo.vue'

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'

// Import Swiper styles
import 'swiper/css'

// import required modules
import { FreeMode, Navigation, Thumbs } from 'swiper/modules'

export default {
  name: 'GalleryPage',
  components: {
    Swiper,
    SwiperSlide,
    ModalVideo,
  },
  props: {
    alchol: {
      type: Boolean,
      required: false,
    },
  },
  data: () => ({
    currentSlide: 0,
    modules: [FreeMode, Navigation, Thumbs],
    thumbsSwiper: null,
    carousel: [
      {
        id: 1,
        img: require('../../../global/assets/chicken.png'),
        link: '/productpage',
        imgstyle: 'video',
        videolink: 'https://www.youtube.com/embed/_J2pcVj7eqM',
        productvideoshow: false,
        alchol: true,
      },
      {
        id: 2,
        img: 'https://i2.momoshop.com.tw/1671702898/goodsimg/0010/910/670/10910670_R1.webp',
        link: '/productpage',
        imgstyle: 'image',
        productvideoshow: false,
      },
      {
        id: 3,
        img: require('../../../global/assets/chicken.png'),
        link: '/productpage',
        imgstyle: 'image',
        productvideoshow: false,
      },
      {
        id: 4,
        img: 'https://i2.momoshop.com.tw/1671702898/goodsimg/0010/910/670/10910670_R1.webp',
        link: '/productpage',
        imgstyle: 'image',
        productvideoshow: false,
      },
      {
        id: 5,
        img: require('../../../global/assets/chicken.png'),
        link: '/productpage',
        imgstyle: 'image',
        productvideoshow: false,
      },
    ],
  }),
  methods: {
    slideTo(val) {
      this.currentSlide = val
    },
    productVideo() {
      if (this.carousel.imgstyle == 'video') {
        return true
      }
    },
    setThumbsSwiper(swiper) {
      this.thumbsSwiper = swiper
    },
  },
}
</script>
<style lang="scss" scoped>
.g-carousel-img {
  cursor: pointer;
  width: auto;
  max-width: none;
}
</style>
